module.exports = {
  VariableDeclarator: function VariableDeclarator(path) {
    var left = path.node.id.name;
    var right = path.node.init;

    if (right != null) {
      // x = y
      if (right.type == 'Identifier') {
        if (dependencies.some(function (dependency) {
          return dependency.name == left;
        }) && left != right.name && !dependencies.some(function (dependency) {
          return dependency.name == right.name;
        })) {
          dependencies.push({
            name: right.name,
            type: 'normal'
          });
        }
      } // x = y * z


      if (right.type == 'BinaryExpression' || right.type == 'CallExpression') {
        path.traverse({
          Identifier: function Identifier(path) {
            if (dependencies.some(function (dependency) {
              return dependency.name == left;
            }) && left != path.node.name && !dependencies.some(function (dependency) {
              return dependency.name == path.node.name;
            })) {
              dependencies.push({
                name: path.node.name,
                type: 'normal'
              });
            }
          }
        });
      }
    }
  }
};