global.heap = {};
heap.snapshots = [];
module.exports = {
  addDependencies: function addDependencies(dependencies) {
    heap.dependencies = dependencies;
  },
  heapSnapshot: function heapSnapshot(id) {
    var snapshot = {
      timeLineId: global.timeLine,
      timePointId: ''
    };
    var originId = id;
    var counter = 0;
    var startFromNumber = global.startFrom;
    var i = 0;

    while (isNaN(parseInt(startFromNumber))) {
      startFromNumber = global.startFrom.slice(i);
      if (i > global.startFrom.length) break;
      ++i;
    }

    if (i <= global.startFrom.length) {
      var startFromName = global.startFrom.slice(0, i - 1);

      if (id == startFromName) {
        counter = parseInt(startFromNumber);
        id = id + ++counter;
      }
    }

    var oldTimePoint;

    while (heap.snapshots.find(function (element) {
      return element.timePointId == id;
    })) {
      oldTimePoint = heap.snapshots.findIndex(function (element) {
        return element.timePointId == id && element.timeLineId != global.timeLine;
      });
      if (oldTimePoint != -1) heap.snapshots.splice(oldTimePoint, 1);else id = originId + ++counter;
    }

    snapshot.timePointId = id;
    heap.dependencies.map(function (dependecy) {
      snapshot["".concat(dependecy.name)] = global[dependecy.name.toString()];
    });
    heap.snapshots.push(snapshot);
  }
};